import React, { useState } from 'react'
import Alert from '@mui/material/Alert'
import { supabase } from '../helper/supabaseClient'
import DOMPurify from 'dompurify'

function NewMarker() {
    const [name, setName] = useState('')
    const [lat, setLat] = useState('')
    const [lng, setLng] = useState('')
    const [popupcontent, setPopupContent] = useState('')
    const [icon, setIcon] = useState('')
    const [alert, setAlert] = useState({ type: '', message: '' })

    const validateText = (text) => /^[a-zA-Z\s0-9.,'!?-]+$/.test(text)
    const validateNumber = (num) => /^-?\d+(\.\d+)?$/.test(num)

    const handleSubmit = async (e) => {
        e.preventDefault()

        const { data, error: userError } = await supabase.auth.getUser()

        if (userError || !data?.user) {
            setAlert({
                type: 'error',
                message: 'Du mste vara inloggad fr att lgga till en plats!'
            })
            return
        }

        const userId = data.user.id

        if (!name || !lat || !lng) {
            setAlert({ type: 'error', message: 'Fyll i alla flt som behvs!' })
            return
        }

        if (
            !validateText(name) ||
            (popupcontent && !validateText(popupcontent))
        ) {
            setAlert({
                type: 'error',
                message:
                    'Endast bokstver, siffror och vanliga tecken r tilltna i textflt!'
            })
            return
        }

        if (!validateNumber(lat) || !validateNumber(lng)) {
            setAlert({
                type: 'error',
                message: 'Latitud och longitud mste vara nummer!'
            })
            return
        }

        const sanitizedPopupContent = DOMPurify.sanitize(popupcontent.trim())

        const { error } = await supabase.from('markers').insert([
            {
                name: name.trim(),
                lat: parseFloat(lat),
                lng: parseFloat(lng),
                popupcontent: sanitizedPopupContent,
                icon: icon,
                user_id: userId
            }
        ])

        if (error) {
            setAlert({
                type: 'error',
                message: 'Ett fel intrffade vid tillgg av ny plats.'
            })
            console.error('Fel vid ny plats:', error)
        } else {
            setAlert({ type: 'success', message: 'Ny plats tillagd!' })
        }
    }

    return (
        <div className="new-marker-container">
            <h3>Lgg till en ny plats</h3>
            {alert.message && (
                <Alert severity={alert.type}>{alert.message}</Alert>
            )}
            <form onSubmit={handleSubmit} className="add-marker-form">
                <label>Namn</label>
                <input
                    type="text"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                />

                <label>Latitud</label>
                <input
                    type="number"
                    step="any"
                    value={lat}
                    onChange={(e) => setLat(e.target.value)}
                    required
                />

                <label>Longitud</label>
                <input
                    type="number"
                    step="any"
                    value={lng}
                    onChange={(e) => setLng(e.target.value)}
                    required
                />

                <label>Beskrivning (frivillig)</label>
                <textarea
                    value={popupcontent}
                    onChange={(e) => setPopupContent(e.target.value)}
                />

                <label>Icon (frivillig)</label>
                <select value={icon} onChange={(e) => setIcon(e.target.value)}>
                    <option value="">Standard</option>
                    <option value="/img/boat.webp">Bt</option>
                    <option value="/img/food.webp">Mat</option>
                    <option value="/img/swim.webp">Simning</option>
                </select>

                <button type="submit">Lgg till</button>
            </form>
        </div>
    )
}

export default NewMarker
